

header{
background-color: transparent;
position: fixed;
z-index: 100;
top: 0;
width: 100%;
min-width: 280px;

#conteinerNavBar{
display: flex;
align-items: center;
flex-direction: row;
position: relative;

padding: 0 40px;
max-width: none;
margin: auto;

font-family: var(--fontTitle);

.ConteinerLogo{

padding: 0 20px;

.BoxLogo{

padding: 0 10px;
min-width: 100px;

img{
width: 100%;
height: auto;
}

}

}

nav{
display: flex;
flex-direction: row;
align-items: center;
justify-content: space-evenly;
z-index: 3;

unicode-bidi: isolate;
padding-left: 50px;
line-height: 1.8;

.ButtonNavBar{

font-size: 16px;
color: #fff;
font-weight: 400;
line-height: 100px;

padding: 0.5em 1em;
text-decoration: none;
text-transform: uppercase;

}

.BoxButton{

position: absolute;
right: 0;

.BtnMobile{
padding-right: 1em;
font-size: 3em;
border: none;
background: none;
color: #fff;

}

}

}

#BoxIcon{

display: flex;
position: absolute;
right:0;
font-size: 1.5em;
color: #fff;
z-index: 3;

padding: 0 40px;

i{
padding: 0 20px;
}

}

}

.ConteinerActived{

background: rgba(255, 255, 255, .9);
box-shadow: 0 0 10px 0 rgba(0, 0, 0, .5);

nav .ButtonNavBar{

color: #000 !important;

}

}

.BoxIconActived{

color: #000 !important;

}

}

#MenuMobile{

display: flex;
align-items: center;
position: fixed;
left: 0;
z-index: 200;
background-color: #fff;
transition: all 500ms ease-in-out;
width: 60%;
height: 100vh;

#conteinerMenuMobile{

display:flex;
flex-direction: column;
align-items: center;
justify-content: space-between;
padding: 0 20px;

width: 80%;
height: 80vh;

.BoxLogo{

padding: 0 20px;

img{
width: 100%;
height: auto;
}

}

nav{

display: flex;
flex-direction: column;
align-items: center;

.ButtonNavBar{

font-size: 16px;
color: #000;
font-weight: 400;
line-height: 80px;
font-family: var(--fontTitle);

padding: 0 0.5em;
text-decoration: none;
text-transform: uppercase;

}


}

#BoxIcon{

display: flex;
font-size: 1.5em;
color: #000;

padding: 0 20px;

i{
padding: 0 10px;
}

}

}

}