#Saudacao{

background: url("../../assets/image/hero_inner_page.jpg")no-repeat;
background-position: 50% 0px;
background-size: cover;
transition-delay: 0ms;
background-attachment: fixed;

padding-top: 12em;
padding-bottom: 3em;
width: 100%;

#ConteinerSaudacao{


max-width: none;
padding-left: 30px;
padding-right: 30px;

.ConteinerTextSaudacao{

max-width: 800px;
display: block;
clear: both;

.BoxText{

text-align: left;
color: #fff;
font-family: var(--fontTitle);

h1{

font-size: 3em;
max-width:6em;
border-bottom: 4px solid #FF0000;
line-height: 1.3;

}

p{

opacity: 0.6;
font-size: 1.1em;
margin-bottom: .5em;
line-height: 1.6;

}

}

}

}

}